'use client';

import { useEffect, useState } from 'react';
import { useAuth } from '@/context/AuthContext';
import { useRouter } from 'next/navigation';
import Sidebar from '@/components/Sidebar';
import Loading from '@/components/Loading';
import api from '@/lib/api';
import { FiCheck, FiX, FiDownload, FiEye } from 'react-icons/fi';

export default function AdminDoctorsPage() {
  const { user, loading: authLoading } = useAuth();
  const router = useRouter();
  const [pendingDoctors, setPendingDoctors] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);
  const [selectedDoctor, setSelectedDoctor] = useState<any>(null);
  const [showModal, setShowModal] = useState(false);
  const [notes, setNotes] = useState('');

  useEffect(() => {
    if (!authLoading && (!user || user.role !== 'admin')) {
      router.push('/dashboard');
    }
  }, [user, authLoading, router]);

  useEffect(() => {
    if (user && user.role === 'admin') {
      fetchPendingDoctors();
    }
  }, [user]);

  const fetchPendingDoctors = async () => {
    try {
      setLoading(true);
      const response = await api.get('/doctors/admin/pending');
      setPendingDoctors(response.data.doctors);
    } catch (error) {
      console.error('Error fetching pending doctors:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleApprove = async (doctorId: string) => {
    try {
      await api.put(`/doctors/admin/${doctorId}/approve`, { notes });
      alert('Doctor approved successfully!');
      setShowModal(false);
      setSelectedDoctor(null);
      setNotes('');
      fetchPendingDoctors();
    } catch (error: any) {
      alert(error.response?.data?.message || 'Failed to approve doctor');
    }
  };

  const handleReject = async (doctorId: string) => {
    if (!notes.trim()) {
      alert('Please provide a reason for rejection');
      return;
    }

    if (!confirm('Are you sure you want to reject this doctor application?')) {
      return;
    }

    try {
      await api.put(`/doctors/admin/${doctorId}/reject`, { notes });
      alert('Doctor application rejected');
      setShowModal(false);
      setSelectedDoctor(null);
      setNotes('');
      fetchPendingDoctors();
    } catch (error: any) {
      alert(error.response?.data?.message || 'Failed to reject doctor');
    }
  };

  const openModal = (doctor: any) => {
    setSelectedDoctor(doctor);
    setShowModal(true);
  };

  if (authLoading || loading) {
    return <Loading />;
  }

  if (!user || user.role !== 'admin') {
    return null;
  }

  return (
    <div className="flex min-h-screen">
      <Sidebar />
      <main className="ml-64 flex-1 p-8">
        <div className="max-w-7xl mx-auto">
          <h1 className="text-3xl font-bold text-gray-800 mb-6">Pending Doctor Applications</h1>

          {pendingDoctors.length === 0 ? (
            <div className="bg-white rounded-lg shadow-md p-8 text-center">
              <p className="text-gray-600 text-lg">No pending doctor applications</p>
            </div>
          ) : (
            <div className="space-y-4">
              {pendingDoctors.map((doctor) => (
                <div
                  key={doctor.id}
                  className="bg-white rounded-lg shadow-md p-6 hover:shadow-lg transition-shadow"
                >
                  <div className="flex justify-between items-start">
                    <div className="flex-1">
                      <div className="flex items-center gap-4 mb-4">
                        <h3 className="text-2xl font-semibold text-gray-800">
                          {doctor.user?.name}
                        </h3>
                        <span className="px-3 py-1 bg-yellow-100 text-yellow-800 rounded-full text-sm font-semibold">
                          Pending Approval
                        </span>
                      </div>

                      <div className="grid grid-cols-2 md:grid-cols-3 gap-4 mb-4">
                        <div>
                          <p className="text-sm text-gray-500">Specialization</p>
                          <p className="font-semibold text-gray-800">{doctor.specialization}</p>
                        </div>
                        <div>
                          <p className="text-sm text-gray-500">Department</p>
                          <p className="font-semibold text-gray-800">
                            {doctor.department?.icon} {doctor.department?.name}
                          </p>
                        </div>
                        <div>
                          <p className="text-sm text-gray-500">Experience</p>
                          <p className="font-semibold text-gray-800">{doctor.experience} years</p>
                        </div>
                        <div>
                          <p className="text-sm text-gray-500">Email</p>
                          <p className="font-semibold text-gray-800">{doctor.user?.email}</p>
                        </div>
                        <div>
                          <p className="text-sm text-gray-500">Phone</p>
                          <p className="font-semibold text-gray-800">{doctor.user?.phone || 'N/A'}</p>
                        </div>
                        <div>
                          <p className="text-sm text-gray-500">Consultation Fee</p>
                          <p className="font-semibold text-gray-800">BDT {doctor.consultationFee || 0}</p>
                        </div>
                      </div>

                      {doctor.qualification && (
                        <div className="mb-4">
                          <p className="text-sm text-gray-500 mb-1">Qualifications</p>
                          <p className="text-gray-800">{doctor.qualification}</p>
                        </div>
                      )}

                      {doctor.bio && (
                        <div className="mb-4">
                          <p className="text-sm text-gray-500 mb-1">Bio</p>
                          <p className="text-gray-800">{doctor.bio}</p>
                        </div>
                      )}

                      {doctor.previousJobs && Array.isArray(doctor.previousJobs) && doctor.previousJobs.length > 0 && (
                        <div className="mb-4">
                          <p className="text-sm text-gray-500 mb-2 font-semibold">Previous Jobs:</p>
                          <div className="space-y-2">
                            {doctor.previousJobs.map((job: any, index: number) => (
                              <div key={index} className="bg-gray-50 p-3 rounded-lg">
                                <p className="font-semibold text-gray-800">{job.position}</p>
                                <p className="text-sm text-gray-600">{job.hospital}</p>
                                <p className="text-xs text-gray-500">{job.years}</p>
                              </div>
                            ))}
                          </div>
                        </div>
                      )}

                      {doctor.education && Array.isArray(doctor.education) && doctor.education.length > 0 && (
                        <div className="mb-4">
                          <p className="text-sm text-gray-500 mb-2 font-semibold">Education:</p>
                          <div className="space-y-2">
                            {doctor.education.map((edu: any, index: number) => (
                              <div key={index} className="bg-gray-50 p-3 rounded-lg">
                                <p className="font-semibold text-gray-800">{edu.degree}</p>
                                <p className="text-sm text-gray-600">{edu.institution}</p>
                                <p className="text-xs text-gray-500">{edu.year}</p>
                              </div>
                            ))}
                          </div>
                        </div>
                      )}

                      {((doctor.availableDays && Array.isArray(doctor.availableDays) && doctor.availableDays.length > 0) || (doctor.availableFrom && doctor.availableTo)) && (
                        <div className="mb-4">
                          <p className="text-sm text-gray-500 mb-2">Availability</p>
                          {doctor.availableDays && Array.isArray(doctor.availableDays) && doctor.availableDays.length > 0 && (
                            <div className="mb-2">
                              <p className="text-xs text-gray-500 mb-1">Days:</p>
                              <div className="flex flex-wrap gap-2">
                                {doctor.availableDays.map((day: string, index: number) => (
                                  <span
                                    key={index}
                                    className="px-2 py-1 bg-teal-100 text-teal-800 rounded-full text-xs font-medium"
                                  >
                                    {day}
                                  </span>
                                ))}
                              </div>
                            </div>
                          )}
                          {doctor.availableFrom && doctor.availableTo && (
                            <p className="font-semibold text-gray-800">
                              Time: {doctor.availableFrom} - {doctor.availableTo}
                            </p>
                          )}
                        </div>
                      )}

                      {(doctor.hasCV || doctor.cvResume) && (
                        <div className="mb-4">
                          <a
                            href={`${process.env.NEXT_PUBLIC_API_URL || 'http://localhost:5000'}/api/doctors/${doctor.id}/cv`}
                            target="_blank"
                            rel="noopener noreferrer"
                            className="inline-flex items-center gap-2 text-teal-600 hover:text-teal-700 font-semibold"
                          >
                            <FiDownload />
                            Download CV/Resume {doctor.cvResumeFileName && `(${doctor.cvResumeFileName})`}
                          </a>
                        </div>
                      )}
                    </div>

                    <div className="flex flex-col gap-2 ml-4">
                      <button
                        onClick={() => openModal(doctor)}
                        className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 flex items-center gap-2"
                      >
                        <FiEye />
                        Review
                      </button>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          )}

          {showModal && selectedDoctor && (
            <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
              <div className="bg-white rounded-lg p-8 max-w-2xl w-full mx-4 max-h-[90vh] overflow-y-auto">
                <h2 className="text-2xl font-bold text-gray-800 mb-4">
                  Review Doctor Application
                </h2>

                <div className="mb-4">
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Notes (Optional for approval, Required for rejection)
                  </label>
                  <textarea
                    value={notes}
                    onChange={(e) => setNotes(e.target.value)}
                    rows={4}
                    className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-teal-500"
                    placeholder="Add any notes or comments..."
                  />
                </div>

                <div className="flex gap-4">
                  <button
                    onClick={() => {
                      setShowModal(false);
                      setSelectedDoctor(null);
                      setNotes('');
                    }}
                    className="flex-1 px-4 py-2 border border-gray-300 rounded-lg hover:bg-gray-50"
                  >
                    Cancel
                  </button>
                  <button
                    onClick={() => handleReject(selectedDoctor.id)}
                    className="flex-1 px-4 py-2 bg-red-600 text-white rounded-lg hover:bg-red-700 flex items-center justify-center gap-2"
                  >
                    <FiX />
                    Reject
                  </button>
                  <button
                    onClick={() => handleApprove(selectedDoctor.id)}
                    className="flex-1 px-4 py-2 bg-teal-600 text-white rounded-lg hover:bg-teal-700 flex items-center justify-center gap-2"
                  >
                    <FiCheck />
                    Approve
                  </button>
                </div>
              </div>
            </div>
          )}
        </div>
      </main>
    </div>
  );
}

